import RejectDialogVue from "@/components/Orders/RejectDialog.vue";
import { createApp } from "vue";

let rejectApp: any;

export function RejectDialog(show: boolean | undefined, order: any): void {
  const rootContainer = document.getElementById("rootContainer")!;
  if (rejectApp) {
    rejectApp.unmount();
  }
  rejectApp = createApp(RejectDialogVue, {
    show: show,
    order: order,
  });
  rejectApp.mount(rootContainer, false);
}
